html{
  --purple: rgb(160,124,253);
  font-family: "Courier New";
  background:black;
}

.App {
  text-align: center;
  background: black;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 50px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.container-photo {
  background:red;
}

.header-image{
  background-color: "black";
  height: 200px;
  width: 100%;
}

.panel{
  -ms-flex:1 1;
      flex:1 1;
  border-color:var(--purple);
  border-width: 1px;
  border-style: solid;
  padding:20px;
  text-align: left;
  background: black;
}

.flex-container {
  padding: 0;
  margin: 0;
  list-style: none;
  
  -ms-box-orient: horizontal;
  display: -ms-flexbox;
  display: -moz-flex;
  display: flex;

}

.flex-row{ 
  -ms-flex-direction: row; 
      flex-direction: row;  
}

.flex-column{ 
  -ms-flex-direction: column; 
      flex-direction: column;    
}

.string-container{
  margin-left: 5%;
  width: 100%;
  color:rgb(223,219,116);
}

.number-container{
  color:var(--purple);
}

.array-container{
  margin-left: 5%;
}

.json-container{
  margin-left: 5%; 
  width: 100%;
}

.code-container{
  background: black;
}

.let{
    color:rgb(104,215,389);
    margin-right: 10px;
}

.portafolio-container{
  color:white;
  font-family: "Courier New";
}

.portafolio-item-container{
  width: 100%;
  margin: 10px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.portafolio-item{
  border-color: white;
  border-style: solid;
  border-width: 1px;
  width: 100%;
  height: 250px;
  border-radius: 5px;
  color:white;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



